import WeatherService from '../weatherService/WeatherService'
import { Component } from 'react';
import './weather.scss'

class Weather extends Component {
  constructor(props) {
    super(props);
    this.updateWeather();
  }

  state = {
    weath: {}
  }

  weatherService = new WeatherService();

  onWeatherLoaded = (weath) => {
    this.setState({weath})
  }

  updateWeather = () => {
    this.weatherService
        .getAllWeatherParam()
        .then(this.onWeatherLoaded)
  } 

  render() {
    const {weath: {weather_id, weather_icon, temp, feels_like}} = this.state;
    const weather_descrRU = () => {
      switch(weather_id) { 
        case 200: return '   '
        case 201: return '  '
        case 202: return '   '
        case 210: return ' '
        case 211: return ''
        case 212: return ' '
        case 221: return ' '
        case 230: return '   '
        case 231: return '  '
        case 232: return '    '
        case 300: return ' '
        case 301: return ''
        case 302: return '  '
        case 310: return '  '
        case 311: return ' '
        case 312: return '  '
        case 313: return '   '
        case 314: return '     '
        case 321: return ' '
        case 500: return ' '
        case 501: return ' '
        case 502: return '  '
        case 503: return '  '
        case 504: return ' '
        case 511: return ' '
        case 520: return '  '
        case 521: return ' '
        case 522: return '  '
        case 531: return '  '
        case 600: return ' '
        case 601: return ''
        case 602: return ' '
        case 611: return ' '
        case 612: return '    '
        case 613: return ' '
        case 615: return '   '
        case 616: return '  '
        case 620: return '  '
        case 621: return ' '
        case 622: return '  '
        case 701: return ''
        case 721: return ''
        case 741: return ''
        case 711: return ''
        case 731: return '/ '
        case 751: return ''
        case 761: return ''
        case 762: return ' '
        case 771: return ''
        case 781: return ''
        case 800: return ' '
        case 801: return ''
        case 802: return ''
        case 803: return ''
        case 804: return ''
        default: return ''
      }
    }
    const url = `http://openweathermap.org/img/wn/${weather_icon}@4x.png`;
    const options = {  year: 'numeric', month: 'long', day: 'numeric', weekday: 'long',timezone: 'UTC'}
    const nowDate = new Date().toLocaleString("ru", options).toUpperCase();
    return (
        <div className="weather-card">
          <div className="date">{nowDate}</div>
          <div className="icon_weather"><img src={url} alt="icon_weather"/></div>
          <div className="weather_description"> : {weather_descrRU().toUpperCase()}</div>
          <div className="temperature">: {Math.round(temp)} C</div>
          <div className="t_feels"> : {Math.round(feels_like)} C</div>
        </div>
    )
  }
}

export default Weather;