import './back.scss'
import Text from './Text'
import React, { Suspense, useRef, Component} from 'react'
import TimeOfYear from '../weatherService/Date'
import WeatherService from '../weatherService/WeatherService'
import { Canvas, render, useFrame } from '@react-three/fiber'
import { Sky, Environment, Cloud, Stars, Plane } from '@react-three/drei'
import { Ground } from './Ground'


const Jumbo = () => {
  const ref = useRef()
  useFrame(({ clock }) => (ref.current.rotation.x = ref.current.rotation.y = ref.current.rotation.z = Math.sin(clock.getElapsedTime()) * 0.1))
    return (
      // <group>
      <group ref={ref}>
        <Text position={[0, 0, 0]} children={TimeOfYear()} />
      </group>
    )
}

export default function Background() {
  return(
    
    <Canvas shadows camera={{ position: [0, 0, 30] }}>
      <ambientLight intensity={0.1} />
      <pointLight position={[10, 10, 10]} />
      {/* <Suspense fallback={null}> */}
        {/* <Ground/> */}
        <Jumbo />
        {/* <Sky /> */}
        <Stars radius={50} depth={50} count={5000} factor={2} saturation={2} fade speed={0.5} />
        {/* <Cloud speed={0.1}  opacity={0.8} position={[-15,10,40]} color={['white']}/> */}
        <Cloud position={[-35, 25, 0]} segments={40} width={20} opacity={0.2}/>
        <Cloud position={[-25, 25, 0]} segments={40} width={10} opacity={0.1}/>
        <Cloud position={[-15, 25, 0]} segments={40} width={10} opacity={0.2}/>
        <Cloud position={[-5, 25, 0]} segments={40} width={10} opacity={0.1}/>
        <Cloud position={[5, 25, 0]} segments={40} width={10} opacity={0.1}/>
        <Cloud position={[15, 25, 0]} segments={40} width={10} opacity={0.2}/>
        <Cloud position={[25, 25, 0]} segments={40} width={10} opacity={0.1}/>
        <Cloud position={[35, 25, 0]} segments={40} width={20} opacity={0.1}/>
        <Environment preset="city" />
      {/* </Suspense> */}
    </Canvas>
  )
}
